.top {
    top: 0px;
  left: 0px;
  height: 100px;
    background: #191919 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 6px #BEBEBE1A;
  opacity: 1;
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  overflow: hidden;
  width: 101vw;
}
.nav-items {
    margin-bottom: 32px;
  padding: 10% 0;
  font: normal normal medium 30px/36px SF Pro Text;
  letter-spacing: 0px;
  color: #F8F6F1;
  opacity: 1;
  text-align: center;
}
.m-t-32 {
  margin-top: 32px;
  
}
.nav-image {
  float: right;
  padding: 0;
}
.m-t-14 {
  margin-top: 14px;
  padding: 0;
}
.m-t-26 {
  margin-top: 26px;
  padding: 0;
}
.center-vertical {
  margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}
.text {
  text-align: center;
  font: normal normal bold 20px/100px SF Pro Display;
  letter-spacing: 0px;
  color: #E5DFD8;
  opacity: 0.03;
  line-height: 1;
  margin-right: 5px;
  margin-left: 5px;
}
.small-text {
  height: 38px;
  text-align: center;
  font: normal normal 600 13px/100px SF Pro Display;
  letter-spacing: 0px;
  color: #AAA6A1;
  opacity: 1;
}
.center-vertical2 {
  margin: 0;
    position: absolute;
    top: 80%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}